import React from 'react'
import Skeleton from 'react-loading-skeleton'

const TableSkeleton = () => {
  return (
    <>
      <div className='return-table-body m-0 pb-2'>
        <Skeleton count={8} height={30} className='mb-4'/>
      </div>
      <div className='pagination-wrap skeleton'>
        <Skeleton count={1} width={200} height={30} className='d-block'/>
        <ul className='itemperpage-box pagination'>
          <li className='page-item border-0'><Skeleton count={1} width={30} height={30} className='d-block'/></li>
          <li className='page-item border-0'><Skeleton count={1} width={30} height={30} className='d-block'/></li>
          <li className='page-item border-0'><Skeleton count={1} width={30} height={30} className='d-block'/></li>
        </ul>
      </div>
    </>
  )
}

export default TableSkeleton